module.exports = function($,window){
    return function(userOpts){
        //====================== options verification ====================
        var defaultOpts = {
            image:"./images/defaultPin.png",
            size:[30,30],
            anchor:[15,30]
        };
        var optionCheckMap = {
            image:[String,'string'],
            size:[Array,'array'],
            anchor:[Array,'array']
        };
        var opts = defaultOpts;
        if(userOpts){
            for(var key in userOpts){
                if(optionCheckMap[key]){
                    if(userOpts[key].constructor != optionCheckMap[key][0]){
                        console.error(' (key:'+key+')  .'+
                            (typeof userOpts[key])+' => '+optionCheckMap[key][1]+' ');
                    }
                }else{
                    printErrType('OPTION');
                    console.error('     ('+key+')  .');
                }
            }
            $.extend(opts,userOpts);
        }

        var self = {};
        self.elem = $("<div></div>").css({
            "position":'absolute',
            "left":-opts.anchor[0]+'px',
            "top":-opts.anchor[1]+'px',
            "width":opts.size[0]+'px',
            "height":opts.size[1]+'px',
            "background-image":"url("+opts.image+")",
            "background-repeat":"no-repeat",
            "background-size":opts.size[0]+'px '+opts.size[1]+'px'
        });
        self.anchor = opts.anchor;
        self.size = opts.size;
        return self;

        //==================== helpers ======================
        function printErrType(type){
            switch(type){
                case 'OPTION':
                    console.error('--- IndoorMap.icon()   ---');
                    break;
                case 'LOAD':
                    break;
            }
        }
    }
};


