/**
 *  implement scaleFactor (prevents z-buffer quirks), polygon offset (blockPadding)
 */
var Offset = require('polygon-offset');
module.exports = function(mapData,$){
    var floors = mapData.floors;
    for(var i=0; i<floors.length; i++){
        var scaleFactor = 1;
        var floor = floors[i];
        var blocks = floor.blocks;
        var floorBoundary = {
            minX:9999999999,minZ:9999999999,maxX:0,maxZ:0
        };
        //boundary    
        for(var j=0; j<blocks.length; j++){
            var block = blocks[j];
            var points = block.points;
            var indexesToBeRemoved = [];
            for(var k=0; k<points.length; k++){
                var point = points[k];
                var nextPoint = (k==points.length-1)?points[0]:points[k+1];
                if(point[0] == nextPoint[0] && point[1] == nextPoint[1]){
                    indexesToBeRemoved.push(k);
                }
                resetBoundary(floorBoundary,point[0], point[1]);
            }
            while(indexesToBeRemoved.length != 0){
                console.log('inWhile');
                var idx = indexesToBeRemoved.pop();
                points.splice(idx,1);
            }
        }

        var maxBoundaryDist = Math.max(floorBoundary.maxX-floorBoundary.minX, floorBoundary.maxZ-floorBoundary.minZ);
        if(maxBoundaryDist > 100){
            //boundary   100 .
            scaleFactor = maxBoundaryDist / 100;
            floor.basePlateHeight /= scaleFactor;
            floor.basePlateOffset /= scaleFactor;
            floor.defaultBlockHeight /= scaleFactor;
            if(floor.defaultBlockPadding) floor.defaultBlockPadding /= scaleFactor;
            for(var j=0; j<blocks.length; j++){
                var block = blocks[j];
                if(block.height) block.height /= scaleFactor;
                if(block.padding) block.padding /= scaleFactor;
                if(block.sprayOffset) {
                    block.sprayOffset[0]/=scaleFactor; block.sprayOffset[1]/=scaleFactor;
                }
                var points = block.points;
                for(var k=0; k<points.length; k++){
                    var point = points[k];
                    point[0] /= scaleFactor;
                    point[1] /= scaleFactor;
                }
            }
        }
        floor.scaleFactor = scaleFactor;

        //set blockPadding
        var toBeRemovedBlocks = [];
        var toBeAddedBlocks = [];
        for(var j=0; j<blocks.length; j++){
            var block = blocks[j];
            var padding = null;
            if(floor.defaultBlockPadding) padding = floor.defaultBlockPadding;
            if(block.padding) padding = block.padding;
            var points = block.points;
            if(padding){
                var pointsClone = [];
                //deep copy of Array
                for(var k=0;k<points.length;k++){
                    pointsClone.push([points[k][0],points[k][1]]);
                }
                pointsClone.push([pointsClone[0][0],pointsClone[0][1]]); //for Offset compatibility
                //apply padding
                var paddedPoints = null;
                try{
                    var offset = new Offset();
                    paddedPoints = offset.data(pointsClone).padding(padding);
                }catch(e){
                    console.error('mapData : blockPadding    .\n'
                        +"         : Floor(floor='"+floor.floor+"').blocks["+j+"]");
                }

                if(paddedPoints){
                    if(paddedPoints.length == 0){
                        console.error('mapData : blockPadding    .\n'
                            +"         : Floor(floor='"+floor.floor+"').blocks["+j+"]");
                        toBeRemovedBlocks.push(j);
                    }else if(paddedPoints.length > 1){
                        console.error('mapData : blockPadding  '+paddedPoints.length+'   .\n'
                            +"         : Floor(floor='"+floor.floor+"').blocks["+j+"]");
                        toBeRemovedBlocks.push(j);
                        for(var k=0; k<paddedPoints.length; k++){
                            var toBeAddedBlock = $.extend({},block);
                            toBeAddedBlock.points = paddedPoints[k];
                            toBeAddedBlock.points.pop();
                            toBeAddedBlocks.push(toBeAddedBlock);
                        }
                    }else{
                        block.points = paddedPoints[0];
                        block.points.pop();
                    }
                }
            }
        }
        while(toBeRemovedBlocks.length!=0){
            var idx = toBeRemovedBlocks.pop();
            block.points.splice(idx,1);
        }
        while(toBeAddedBlocks.length!=0){
            var toBeAddedBlock = toBeAddedBlocks.shift();
            blocks.push(toBeAddedBlock);
        }
    }
    //=================helpers==================
    function resetBoundary(boundary,x,z){
        boundary.minX=(x<boundary.minX)?x:boundary.minX;
        boundary.minZ=(z<boundary.minZ)?z:boundary.minZ;
        boundary.maxX=(x>boundary.maxX)?x:boundary.maxX;
        boundary.maxZ=(z>boundary.maxZ)?z:boundary.maxZ;
    }
    return mapData;
};



