var THREE = require('./three.min.js');

module.exports = function(maps, mapObjects, $){ // $ is used for loading svg (creating ghost canvas)
    //usedForLoad IMC.load()     boolean load   scaleFactor X
    //image  asynchronous  maps mapObjects  .
    //TODO spray        .
    return function(position, userOpts, currentFloor, usedForLoad){
        var self = {};

        //====================== options verification ====================
        var defaultOpts = {
            image: "./brand-images/zara.png",
            size:null,
            anchor:null,
            floor:currentFloor
        };
        var optionCheckMap = {
            image:[String,'string'],
            size:[Array,'array'],
            anchor:[Array,'array'],
            floor:[String,'string']
        };
        var opts = defaultOpts;
        if(userOpts){
            for(var key in userOpts){
                if(optionCheckMap[key]){
                    if(userOpts[key].constructor != optionCheckMap[key][0]){
                        console.error(' (key:'+key+')  .'+
                            (typeof userOpts[key])+' => '+optionCheckMap[key][1]+' ');
                    }
                }else{
                    printErrType('OPTION');
                    console.error('     ('+key+')  .');
                }
            }
            $.extend(opts,userOpts);
        }

        //==================== create threeJS object ====================
        if(opts.image.match(/.svg$/g)){
            //svg
            var $ghostCanvas = $("<canvas></canvas>");
            var ghostCanvas = $ghostCanvas[0];
            var ctx = ghostCanvas.getContext('2d');
            var img = new Image();
            img.onload = function(){
                ghostCanvas.width = img.width;
                ghostCanvas.height = img.height;
                ctx.drawImage(img, 0, 0);
                new THREE.TextureLoader().load(ghostCanvas.toDataURL(), applyTexture);
            };
            img.src = opts.image;
        }else{
            //image
            new THREE.TextureLoader().load(opts.image, applyTexture);
        }
        function applyTexture(texture){
            texture.minFilter = THREE.LinearFilter;
            var iw = texture.image.width;
            var ih = texture.image.height;
            var ir = ih/iw;

            //adjustment of size, anchor, position
            var scaleFactor = (usedForLoad)? 1 : maps[opts.floor].scaleFactor; //load  scaleFactor  .
            var size = [0,0];
            if(opts.size){
                if(opts.size[1]){
                    size = [opts.size[0]/scaleFactor, opts.size[1]/scaleFactor];
                }else{
                    //      .
                    size = [opts.size[0]/scaleFactor, opts.size[0]*ir/scaleFactor];
                }
            }else{
                size = [iw/scaleFactor, ih/scaleFactor];
            }

            var anchor = (opts.anchor)? [opts.anchor[0]/scaleFactor, opts.anchor[1]/scaleFactor]
                :[size[0]/2, size[1]/2];
            for(var i=0; i<position.length; i++){
                position[i] = position[i]/scaleFactor + 0.05;
            }
            if(!position[2]) position[2] = 0.05;

            //create spray object
            var sprayGeom = new THREE.PlaneGeometry(size[0],size[1]);
            var sprayMaterial = new THREE.MeshBasicMaterial({
                map:texture,
                transparent:true
            });
            var sprayMesh = new THREE.Mesh(sprayGeom, sprayMaterial);
            sprayMesh.rotation.x = -Math.PI/2;
            sprayMesh.position.x = position[0] + size[0]/2 - anchor[0];
            sprayMesh.position.z = position[1] + size[1]/2 - anchor[1];
            sprayMesh.position.y = position[2];

            self.sprayMesh = sprayMesh;
            mapObjects[opts.floor].getObjectByName('sprays').add(sprayMesh);
        }

        return self;

        //==================== helpers ======================
        function printErrType(type){
            switch(type){
                case 'OPTION':
                    console.error('--- IndoorMap.setSpray()   ---');
                    break;
                case 'LOAD':
                    break;
            }
        }
    };
};


