// console.log(require('./IndoorMap_core.js'));
require('./requestAnimationFramePolyfill.js');
require('./IndoorMapStyle.scss');
(function($,window){
    var Promise = require('promise');
    window.IndoorMap = function($parent,userOpts,fallBack){
        //check if webgl available
        if(!require('detector-webgl')){
            console.log('webGL     .');
            if(fallBack) return fallBack();
        }

        //for safety; confirm $parent is a jQuery Object
        $parent = $($parent);
        //default options
        var defaultOpts = {
            backgroundColor:0xefefef,
            tiltAngle:60,
            zoomValue:100,
            zoomStep:40,
            minZoom:50,
            maxZoom:300,
            zoomAnimation:100,
            zoomOnMouseWheel:true,
            pinchZoom:true,
            twoFingerRotation:true,
            zoomController:true,
            zoomControllerCustomClass:null,
            zoomControllerPosition:'topright',
            autoFit:true,
            floorController:true,
            floorControllerCustomClass:null,
            floorControllerPosition:'bottomright',
            limitTranslation:true,
            markerClustering:true,
            markerClusteringAnimation:100,
            onFloorChanged:null,
            onBlockMouseOver:null,
            onBlockMouseOut:null,
            onBlockClick:null
        };
        var opts = defaultOpts;
        //==================== option verification ====================
        if(userOpts){
            if(userOpts.backgroundColor && typeof userOpts.backgroundColor == 'string'){
                userOpts.backgroundColor = parseInt(userOpts.backgroundColor, 16);
            }

            //option check map
            var optionCheckMap = {
                backgroundColor:'number',   //implemented
                tiltAngle:'number',   //implemented
                zoomValue:'number',     //implemented
                zoomStep:'number',
                minZoom:'number',       //implemented
                maxZoom:'number',       //implemented
                zoomAnimation:'number',//implemented
                zoomOnMouseWheel:'boolean', //implemented
                pinchZoom:'boolean',    //implemented
                twoFingerRotation:'boolean',    //implemented
                zoomController:'boolean',   //implemented
                zoomControllerCustomClass:'string', //implemented
                zoomControllerPosition:'string',    //implemented
                autoFit:'boolean',                  //implemented
                floorController:'boolean',  //implemented
                floorControllerCustomClass:'string',    //implemented
                floorControllerPosition:'string',   //implemented
                limitTranslation:'boolean',
                markerClustering:'boolean',
                markerClusteringAnimation:'number',
                onFloorChanged:'function',
                onBlockMouseOver:'function',
                onBlockMouseOut:'function',
                onBlockClick:'function'
            };
            var positionCheckMap = {'topleft':true,'topright':true,'bottomleft':true,'bottomright':true};
            for(var key in userOpts){
                if(optionCheckMap[key]){
                    if(typeof userOpts[key] != optionCheckMap[key]){
                        printErrType('OPTION');
                        console.error(' (key:'+key+')  .'+
                            (typeof userOpts[key])+'=>'+optionCheckMap[key]+' ');
                    }
                }else{
                    printErrType('OPTION');
                    console.error('     ('+key+')  .');
                }
                switch (key) {
                    case 'tiltAngle':
                        if(!(0<=userOpts['tiltAngle'] && userOpts['tiltAngle']<=90)){
                            printErrType('OPTION');
                            console.error('tiltAngle 0 90    . :'+userOpts['tiltAngle']);
                        }
                        break;
                    case 'floorControllerPosition':
                    case 'zoomControllerPosition':
                        if(!positionCheckMap[userOpts[key]]){
                            console.log('positionCheckFailed');
                            printErrType('OPTION');
                            console.error(key+'   ('+userOpts[key]+')    .');
                        }
                        break;
                }
            }
            //merge defaultOpts + userOpts => opts
            $.extend(opts,userOpts);
        }
        //positioning helper
        var controllerOffset = 20;
        var controllerPositionCss = {
            'topleft':{
                'position':'absolute',
                'top':controllerOffset,
                'left':controllerOffset
            },
            'topright':{
                'position':'absolute',
                'top':controllerOffset,
                'right':controllerOffset
            },
            'bottomleft':{
                'position':'absolute',
                'bottom':controllerOffset,
                'left':controllerOffset
            },
            'bottomright':{
                'position':'absolute',
                'bottom':controllerOffset,
                'right':controllerOffset
            }
        };
        //==================== inject container DIV ====================
        var $container = $('<div></div>');
        $container.css({'position':'relative', 'width':'100%', 'height':'100%',
            'background-color':'#'+opts.backgroundColor.toString(16)});
        $parent.append($container);
        //==================== initialize IndoorMap_core ====================
        var IMC = require('./IndoorMap_core.js')($,window,$container,opts);

        //==================== initialize controller UI ====================
        var $zoomController = null;
        var $zoomInBtn = null;
        var $zoomOutBtn = null;
        if(opts.zoomController){
            var $zoomBtn = $('<div class="IndoorMap-zoomBtn"></div>').css({'display':'block'});
            $zoomInBtn = $zoomBtn.clone().text('+').addClass('IndoorMap-zoomInBtn');
            $zoomOutBtn = $zoomBtn.clone().text('').addClass('IndoorMap-zoomOutBtn');
            $zoomController = $('<div></div>').append($zoomInBtn).append($zoomOutBtn);
            if(opts.zoomControllerCustomClass){
                $zoomInBtn.addClass(opts.zoomControllerCustomClass);
                $zoomOutBtn.addClass(opts.zoomControllerCustomClass);
            }
            $zoomController.css(controllerPositionCss[opts.zoomControllerPosition]);
            $container.append($zoomController);
        }
        //==================== subClass =====================
        var icon = require('./IndoorMap_icon.js')($,window);
        var defaultIcon = icon();
        var marker = require('./IndoorMap_marker.js')($,window,defaultIcon);
        //==================== functions to be consumed ====================
        function load(mapData){
            var loadPromise = new Promise(function(resolve,reject){
                if(mapData.constructor == String){
                    $.getJSON(mapData)
                        .done(function(data){
                            loadPromise = loadMapData(data,function(){resolve()});
                        })
                        .fail(function(data){
                            if(data.statusText == 'OK'){
                                printErrType('LOAD');
                                console.error('JSON     .(invalid JSON)');
                            }else{
                                printErrType('LOAD');
                                console.error("'"+mapData+"'    .")
                            }
                        });
                }else{
                    loadMapData(data,resolve);
                }
            });
            return chainObjFactory(loadPromise);
        }

        var $floorController = null;
        function loadMapData(mapData,cb){
            IMC.load(mapData);
            //---------- append floorController ----------
            if(opts.floorController){
                if(opts.floorController){
                    $floorController = $('<div></div>').css(controllerPositionCss[opts.floorControllerPosition]);
                    if(opts.floorControllerCustomClass) $floorController.addClass(opts.floorControllerCustomClass);
                    $container.append($floorController);
                }
                var $floorBtn = $('<div class="IndoorMap-floorBtn"></div>');
                for(var i=0;i<mapData.floors.length;i++){
                    var targetFloor = mapData.floors[i];
                    var $newFloorBtn = $floorBtn.clone().text(targetFloor.name);
                    $floorController.append($newFloorBtn);
                    $newFloorBtn.on('mousedown',
                        (function(floor){
                            return function(e){
                                IMC.changeFloor(floor);
                            }
                        })(targetFloor.floor)
                    );
                }
            }
            //---------- set eventHandlers ----------
            var dragging = false;
            var tabPossibility = 0;  // tab or click  
            var INITIAL_TAB_POSSIBILITY = 5;
            //button handlers
            $zoomInBtn.on('mousedown',function(e){
                IMC.stepZoom(null,opts.zoomStep,opts.zoomAnimation);
            });
            $zoomOutBtn.on('mousedown',function(e){
                IMC.stepZoom(null,-opts.zoomStep,opts.zoomAnimation);
            });
            //mouse translate
            IMC.markerLayer.on('mousedown',function(e){
                if(IMC.isPointInMap(e.pageX,e.pageY)){
                    tabPossibility = INITIAL_TAB_POSSIBILITY;
                    dragging = true;
                    var mapPoint = IMC.pagePoint2Map(e.pageX,e.pageY);
                    // IMC.test(e.pageX,e.pageY); //TODO must remove this test line
                    IMC.interaction.init([mapPoint]);
                    e.preventDefault();
                }
            });
            IMC.markerLayer.on('mousemove',function(e){
                if(dragging){
                    tabPossibility--;
                    var mapPoint = IMC.pagePoint2Map(e.pageX,e.pageY);
                    IMC.interaction.interact([mapPoint]);
                    e.preventDefault();
                }else{
                    //block interactions
                    IMC.interactionWithBlocks('mousemove',IMC.pagePoint2Map(e.pageX,e.pageY));
                }
            });
            $(window).on('mouseup',function(e){
                if(dragging) {
                    dragging = false;
                    if(tabPossibility>0) IMC.interactionWithBlocks('click',IMC.pagePoint2Map(e.pageX,e.pageY));
                }
            });
            //mouse zoom (scroll)
            if(opts.zoomOnMouseWheel){
                IMC.markerLayer.on('mousewheel',function(e){
                    var event = e.originalEvent;
                    if(event.wheelDelta){
                        IMC.stepZoom(IMC.pagePoint2Map(e.pageX,e.pageY),event.wheelDelta/10);
                        e.preventDefault();
                    }
                });
            }
            //touch translate, zoom, pinch
            var prevFingersNum = 0;
            IMC.markerLayer.on('touchstart',function(e){
                var event = e.originalEvent;
                var mapPoints = IMC.pageTouches2Maps(event.touches);

                //tabPossibility control
                var currFingersNum = mapPoints.length;
                if(prevFingersNum == 0) tabPossibility = INITIAL_TAB_POSSIBILITY;
                if(currFingersNum > 1) tabPossibility = 0;
                prevFingersNum = currFingersNum;

                IMC.interaction.init(mapPoints);
                if(mapPoints.length>1) tabPossibility = 0;
                e.preventDefault();
            });
            IMC.markerLayer.on('touchmove',function(e){
                var event = e.originalEvent;
                var mapPoints = IMC.pageTouches2Maps(event.touches);

                //tabPossibility control
                tabPossibility--;

                IMC.interaction.interact(mapPoints);
                e.preventDefault();
            });
            IMC.markerLayer.on('touchend',function(e){
                var event = e.originalEvent;
                var mapPoints = IMC.pageTouches2Maps(event.touches);

                //tabPossibility control
                var currFingersNum = mapPoints.length;
                console.log(tabPossibility);
                if(tabPossibility>0) IMC.interactionWithBlocks('click',IMC.pagePoint2Map(e.pageX,e.pageY));
                prevFingersNum = currFingersNum;

                IMC.interaction.init(mapPoints);
                e.preventDefault();
            });
            cb();
        }

        function setMarker(position,opts){
            //apply scaleFactor
            for(var i=0;i<position.length;i++){
                position[i] /= IMC.getCurrentMap().scaleFactor;
            }
            var newMarker = marker(position,opts);
            IMC.addMarker(newMarker,opts);
            return newMarker;
        }
        function removeMarker(marker){
            IMC.removeMarker(marker);
        }
        function removeMarkersOn(floor){
            IMC.removeMarkersOn(floor);
        }
        function removeAllMarkers(){
            IMC.removeAllMarkers();
        }
        function setSpray(position, opts){
            IMC.setSpray(position, opts);
        }
        function removeSpraysOn(floor){
            IMC.removeSpraysOn(floor);
        }
        function removeAllSprays(){
            IMC.removeAllSprays();
        }
        function setPolyline(points,opts){
            //polyline points performance  IMC scaleFactor .
            return IMC.setPolyline(points,opts);
        }
        function removePolyline(polyline){
            IMC.removePolyline(polyline);
        }
        function removePolylinesOn(floor){
            IMC.removePolylinesOn(floor);
        }
        function removeAllPolylines(){
            IMC.removeAllPolylines();
        }
        function getBlock(id,floor){
            return IMC.getBlock(id,floor);
        }
        function getBlocksOn(floor){
            return IMC.getBlocksOn(floor);
        }
        function getAllBlocks(){
            return IMC.getAllBlocks();
        }
        function setView(_zoomLevel, _center, _animation){
            return new Promise(function(resolve,reject){
                var centerVector = null;
                if(_center){
                    centerVector = IMC.Point(_center[0],_center[1]);
                    //apply scaleFactor
                    centerVector.multiplyScalar(1/IMC.getCurrentMap().scaleFactor);
                }
                IMC.setView(_zoomLevel, centerVector, _animation, resolve);
            });
        }
        function setTiltAngle(_tiltAngle, _animation){
            return new Promise(function(resolve,reject){
                IMC.setTiltAngle(_tiltAngle, _animation, resolve);
            });
        }
        function rotate(rotationAngle, center, animation){
            if(center){
                for(var i=0;i<center.length;i++){
                    center[i]/=IMC.getCurrentMap().scaleFactor;
                }
            }else{
                center = [null,null];
            }
            return new Promise(function(resolve,reject){
                IMC.externalRotate(rotationAngle,center,animation,resolve);
            });
        }
        //==================== helpers ====================
        function printErrType(type){
            switch(type){
                case 'OPTION':
                    console.error('--- IndoorMap()   ---');
                    break;
                case 'LOAD':
                    console.error('--- .load()  ---');
                    break;
            }
        }
        var self = {
            load:load,
            setView:setView,
            setTiltAngle:setTiltAngle,
            rotate:rotate,
            icon:icon,
            setPolyline:setPolyline,
            removePolyline:removePolyline,
            removeAllPolylines:removeAllPolylines,
            removePolylinesOn:removePolylinesOn,
            setSpray:setSpray,
            removeSpraysOn:removeSpraysOn,
            removeAllSprays:removeAllSprays,
            setMarker:setMarker,
            removeMarker:removeMarker,
            removeMarkersOn:removeMarkersOn,
            removeAllMarkers:removeAllMarkers,
            getBlock:getBlock,
            getBlocksOn:getBlocksOn,
            getAllBlocks:getAllBlocks
        };
        function chainObjFactory(promise){
            var chainingObj = {};
            var chainableMethods = ['load','setView','setTiltAngle','rotate'];
            for(var i=0;i<chainableMethods.length;i++){
                chainingObj[chainableMethods[i]] = (function(methodName){
                    return function(){
                        var args = arguments;
                        var chainingPromise = new Promise(function(resolve,reject){
                            promise.then(function(){
                                var nextPromise = self[methodName].apply(self,args);
                                nextPromise.then(function(){
                                    resolve();
                                });
                            });
                        });
                        return chainObjFactory(chainingPromise);
                    };
                })(chainableMethods[i]);
            }
            chainingObj.done = function(cb){
                var chainingPromise = new Promise(function(resolve,reject){
                    promise.then(function(){
                        cb.apply(self,[resolve]);
                    });
                });
                return chainObjFactory(chainingPromise);
            };
            chainingObj.delay = function(delayTime){
                var chainingPromise = new Promise(function(resolve,reject){
                    promise.then(function(){
                        setTimeout(function(){resolve();},delayTime);
                    });
                });
                return chainObjFactory(chainingPromise);
            };
            return chainingObj;
        }
        return self;
    };
})(jQuery,window);



