
module.exports = function($,window,defaultIcon){
    return function(position,userOpts){
        var self = {};
        //====================== options verification ====================
        var defaultOpts = {
            type: "both",
            priority: 1,
            icon:defaultIcon,
            //floor: currentFloor (IndoorMap_core )
            html:"<div style='color:#ff0000;'> </div>",
            align:"center bottom",
            padding: 5
        };
        var optionCheckMap = {
            id:[String,'string'],
            type:[String,'string'],
            priority:[Number,'number'],
            floor:[String,'string'],
            icon:[Object,'object'],
            html:[String,'string'],
            align:[String,'string'],
            padding:[Number,'number'],
            className:[String,'string'],
            mouseover:[Function,'function'],
            mouseout:[Function,'function'],
            click:[Function,'function']
        };

        var opts = defaultOpts;

        //==================== validate userOpts ====================
        if(userOpts){
            for(var key in userOpts){
                if(optionCheckMap[key]){
                    if(userOpts[key].constructor != optionCheckMap[key][0]){
                        console.error(' (key:'+key+')  .'+
                            (typeof userOpts[key])+'=>'+optionCheckMap[key][1]+' ');
                    }
                }else{
                    printErrType('OPTION');
                    console.error('     ('+key+')  .');
                }
            }
        }
        $.extend(opts, userOpts);

        //==================== vars ====================
        self.position = position;
        self.hidden = false; //used for reclustering
        self.priority = opts.priority;
        self.forceToShow = false; //for disable reclustering

        var elemPosition = [0,0];
        var $htmlElem = null;
        var $iconElem = null;

        //==================== create marker elem ====================
        var elem = $("<div></div>");
        self.elem = elem;
        elem.css({
            position:'absolute',
            whiteSpace:'nowrap'
        });
        if(opts.id){
            elem.attr('id',opts.id);
        }
        if(opts.className){
            elem.addClass(opts.className);
        }
        if(opts.mouseover){
            elem.on('mouseover',function(){
                opts.mouseover.apply(self,[self]);
            });
        }
        if(opts.mouseout){
            elem.on('mouseout', function(){
                opts.mouseout.apply(self,[self]);
            });
        }
        if(opts.click){
            elem.on('click', function(){
                opts.click.apply(self,[self]);
            });
        }

        //==================== append html|icon to marker elem ====================
        switch(opts.type){
            case 'both':
                appendIcon(elem,opts.icon.elem.clone(),opts.icon.anchor);
                appendHTML(elem,opts.html,opts.align);
                break;
            case 'icon':
                appendIcon(elem,opts.icon.elem.clone(),opts.icon.anchor);
                break;
            case 'html':
                appendHTML(elem,opts.html,opts.align);
                break;
        }
        function appendIcon($marker,$icon){
            $iconElem = $icon;
            $icon.hide();
            $marker.append($icon);
            $icon.show();
        }

        function appendHTML($marker,htmlStr){
            if(htmlStr.match(/<([A-Z][A-Z0-9]*)\b[^>]*>(.*?)<\1>/i)){
                //html tag
                $htmlElem = $(htmlStr);
            }else{
                //plain text
                $htmlElem = $("<div>"+htmlStr+"</div>");
            }
            $htmlElem.css({
                position:'relative',
                'user-select':'none',
                '-moz-user-select':'none',
                'webkit-user-select':'none',
                '-ms-user-select':'none',
                'cursor':'default'
            });
            $marker.append($htmlElem);
        }

        //==================== alignment & reset boundary ====================
        var relativeBoundary = {
            x1:0,
            x2:0,
            y1:0,
            y2:0
        };
        function resetRelativeBoundary(){
            relativeBoundary = {x1:0,x2:0,y1:0,y2:0}
        }
        function recalculateRelativeBoundary(x1,x2,y1,y2){
            var rb = relativeBoundary;
            if(x1 && x1<rb.x1) rb.x1 = x1;
            if(x2 && x2>rb.x2) rb.x2 = x2;
            if(y1 && y1<rb.y1) rb.y1 = y1;
            if(y2 && y2>rb.y2) rb.y2 = y2;
        }
        var horizontalAlign = null;
        var verticalAlign = null;
        self.resetAlign = function(){
            if($htmlElem){
                var htmlElemW = $htmlElem.width();
                var htmlElemH = $htmlElem.height();
                var iconSize = (opts.icon)? opts.icon.size:[0,0];
                var iconAnchor = (opts.icon)? opts.icon.anchor:[0,0];

                if(!(horizontalAlign && verticalAlign)){
                    horizontalAlign = 'center';
                    verticalAlign = 'bottom';
                    if(opts.align){
                        var splittedAlign = opts.align.split(' ');
                        horizontalAlign = splittedAlign[0];
                        verticalAlign = splittedAlign[1];
                    }
                    resetRelativeBoundary();
                    recalculateRelativeBoundary(-iconAnchor[0],iconSize[0]-iconAnchor[0],-iconAnchor[1],iconSize[1]-iconAnchor[1]);
                    switch(horizontalAlign){
                        case 'left':
                            $htmlElem.css({left:-htmlElemW+'px'});
                            recalculateRelativeBoundary(-htmlElemW,0);
                            break;
                        case 'center':
                            $htmlElem.css({left:-htmlElemW/2+'px'});
                            recalculateRelativeBoundary(-htmlElemW/2,htmlElemW/2);
                            break;
                        case 'right':
                            $htmlElem.css({left:0});
                            recalculateRelativeBoundary(0,htmlElemW);
                            break;
                    }
                    switch(verticalAlign){
                        case 'top':
                            $htmlElem.css({top:-iconAnchor[1]-opts.padding-htmlElemH+'px'});
                            recalculateRelativeBoundary(null,null,-iconAnchor[1]-opts.padding-htmlElemH,null);
                            break;
                        case 'middle':
                            $htmlElem.css({top:-htmlElemH/2+'px'});
                            recalculateRelativeBoundary(null,null,-htmlElemH/2,htmlElemH/2);
                            break;
                        case 'bottom':
                            $htmlElem.css({top:iconSize[1]-iconAnchor[1]+opts.padding+'px'});
                            recalculateRelativeBoundary(null,null,iconSize[1]-iconAnchor[1]+opts.padding,iconSize[1]-iconAnchor[1]+opts.padding+htmlElemH);
                            break;
                    }
                }
            }
        };
        self.setClustering = function(enable){
            if(enable){
                self.forceToShow = false;
            }else{
                self.elem.show();
                self.forceToShow = true;
            }
        };
        self.getBoundary = function(){
            return {
                x1:elemPosition[0] + relativeBoundary.x1,
                x2:elemPosition[0] + relativeBoundary.x2,
                y1:elemPosition[1] + relativeBoundary.y1,
                y2:elemPosition[1] + relativeBoundary.y2
            };
        };

        self.$htmlElem = $htmlElem;
        self.$iconElem = $iconElem;
        
        
        
        //==================== helpers ======================
        function printErrType(type){
            switch(type){
                case 'OPTION':
                    console.error('--- IndoorMap.setMarker()   ---');
                    break;
                case 'LOAD':
                    break;
            }
        }
        self.repositionElem = function(point){
            elemPosition[0] = point[0];
            elemPosition[1] = point[1];
            elem.css({
                top:point[1]+'px',
                left:point[0]+'px'
            });
        };

        return self;
    }
};



