var THREE = require('./three.min.js');
module.exports = function(maps, mapObjects, TWEEN){
    //required blockProps : id, height, color, parentFloor, points
    //optional blockProps : spray
    //created blockProps : blockMesh
    return function(blockIdx, blockData, floorId, floorData){
        var self = {};
        //required props
        self.id = (blockData.id)? blockData.id : floorId+'_'+blockIdx;
        self.originalHeight = (blockData.height)?blockData.height:floorData.defaultBlockHeight;
        self.height = self.originalHeight;
        self.originalColor = (blockData.color)?parseInt(blockData.color):parseInt(floorData.defaultBlockColor);
        self.color = self.originalColor;
        self.originalOpacity = (blockData.opacity)?blockData.opacity:
            (floorData.defaultOpacity)?floorData.defaultBlockOpacity:1;
        self.opacity = self.originalOpacity;
        self.parentFloor = floorData.floor;
        self.parentObject = mapObjects[self.parentFloor].getObjectByName('blocks');
        self.points = blockData.points;
        //optional props
        self.spray = null;
        //creating props
        self.boundary = {
            minX:9999999999,minZ:9999999999,maxX:0,maxZ:0
        };
        self.blockMesh = createBlockMesh(blockData.points, self.originalHeight, self.originalColor, self.originalOpacity, blockData.data, true);
        self.heightTween = new TWEEN.Tween();
        self.colorTween = new TWEEN.Tween();
        self.opacityTween = new TWEEN.Tween();

        self.parentObject.add(self.blockMesh);

        //methods for internal
        self.setSpray = function(newSpray){
            self.spray = newSpray;
        };

        //methods for consumer
        self.setData = function(data){
            blockData.data = data;
            self.blockMesh.data = data;
        };
        self.setOpacity = function(opacity, duration){
            self.opacityTween.stop();
            if(!duration){
                self.opacity = opacity;
                rerenderBlock();
            }else{
                self.opacityTween = new TWEEN
                    .Tween({opacity:self.opacity})
                    .to({opacity:opacity}, duration)
                    .onUpdate(function(){
                        self.opacity = this.opacity;
                        rerenderBlock();
                    });
                self.opacityTween.start();
            }
        };
        self.restoreOpacity = function(duration){
            if(!duration){
                self.opacityTween.stop();
                rerenderBlock();
            }else{
                self.opacityTween = new TWEEN
                    .Tween({opacity:self.opacity})
                    .to({opacity:self.originalOpacity}, duration)
                    .onUpdate(function(){
                        self.opacity = this.opacity;
                        rerenderBlock();
                    });
                self.opacityTween.start();
            }
        };
        self.setColor = function(color,duration){
            color = parseInt(color);
            self.colorTween.stop();
            if(!duration){
                self.color = color;
                rerenderBlock();
            }else{
                self.colorTween = new TWEEN
                    .Tween(sliceColorCode(self.color))
                    .to(sliceColorCode(color),duration)
                    .onUpdate(function(){
                        self.color = mergeColorCode(this);
                        rerenderBlock();
                    });
                self.colorTween.start();
            }
        };
        self.restoreColor = function(duration){
            self.colorTween.stop();
            if(!duration){
                self.color = self.originalColor;
                rerenderBlock();
            }else{
                self.colorTween = new TWEEN
                    .Tween(sliceColorCode(self.color))
                    .to(sliceColorCode(self.originalColor),duration)
                    .onUpdate(function(){
                        self.color = mergeColorCode(this);
                        rerenderBlock();
                    });
                self.colorTween.start();
            }
        };
        self.setHeight = function(height,duration){
            self.heightTween.stop();
            if(!duration){
                self.height = height/floorData.scaleFactor;
                rerenderBlock();
            }else{
                self.heightTween = new TWEEN
                    .Tween({ blockHeight: self.height })
                    .to({ blockHeight: height/floorData.scaleFactor }, duration)
                    .onUpdate(function(){
                        self.height = this.blockHeight;
                        rerenderBlock();
                    });
                self.heightTween.start();
            }
        };
        self.restoreHeight = function(duration){
            self.heightTween.stop();
            if(!duration){
                self.height = self.originalHeight;
                rerenderBlock();
            }else{
                self.heightTween = new TWEEN
                    .Tween({ blockHeight: self.height })
                    .to({ blockHeight: self.originalHeight }, duration)
                    .onUpdate(function(){
                        self.height = this.blockHeight;
                        rerenderBlock();
                    });
                self.heightTween.start();
            }
        };
        self.on = function(eventName, handler){
            switch(eventName){
                case 'mouseover':
                    setMouseOverHandler(handler);
                    break;
                case 'mouseout':
                    setMouseOutHandler(handler);
                    break;
                case 'click':
                    setClickHandler(handler);
                    break;
            }
        };


        function setMouseOverHandler(handler){
            self.onMouseOver = function(){
                handler.apply(self,[blockData.data]);
            };
        }
        function setMouseOutHandler(handler){
            self.onMouseOut = function(){
                handler.apply(self,[blockData.data]);
            };
        }
        function setClickHandler(handler){
            self.onClick = function(){
                handler.apply(self,[blockData.data]);
            };
        }

        //---------- helpers ----------
        function rerenderBlock(){
            self.parentObject.remove(self.blockMesh);
            self.blockMesh = createBlockMesh(blockData.points, self.height, self.color, self.opacity, blockData.data, false);
            self.parentObject.add(self.blockMesh);
            if(self.spray) self.spray.sprayMesh.position.y = self.height+0.05;
        }
        function createBlockMesh(points, height, color, opacity, data, shouldResetBoundary){
            var blockShape = new THREE.Shape();
            var extrudeSettings = {
                amount:height,
                bevelEnabled:false
            };
            //draw a block
            var x0 = points[0][0];
            var z0 = points[0][1];
            if(shouldResetBoundary) resetBoundary(self.boundary,x0,z0);
            blockShape.moveTo(x0,z0);
            for(var k=1;k<points.length;k++){
                var x = points[k][0];
                var z = points[k][1];
                if(shouldResetBoundary) resetBoundary(self.boundary,x,z);
                blockShape.lineTo(x,z);
            }
            if(shouldResetBoundary){
                resetBoundary(floorData.boundary,self.boundary.minX,self.boundary.minZ);
                resetBoundary(floorData.boundary,self.boundary.maxX,self.boundary.maxZ);
            }
            blockShape.lineTo(x0,z0);

            var blockGeom = new THREE.ExtrudeGeometry(blockShape,extrudeSettings);
            if(opacity && opacity < 1){
                var blockMaterial = new THREE.MeshLambertMaterial({
                    color:color,
                    transparent:true,
                    opacity:opacity
                });
            }else{
                var blockMaterial = new THREE.MeshLambertMaterial({
                    color:color
                });
            }
            var blockMesh = new THREE.Mesh(blockGeom, blockMaterial);
            if(data) blockMesh.data = data;
            blockMesh.blockObject = self;

            blockMesh.rotation.x = Math.PI/2;
            blockMesh.position.y = height;

            return blockMesh;
        }
        function sliceColorCode(hexNum){
            //separate to R, G, B
            var result = {};
            result.b = hexNum % 0x100;
            result.g = ((hexNum % 0x10000) - result.b)/0x100;
            result.r = (hexNum - (result.b + result.g))/0x10000;
            return result;
        }
        function mergeColorCode(sliced){
            var result = Math.round(sliced.r) * 0x10000 + Math.round(sliced.g) * 0x100 + Math.round(sliced.b);
            if(result > 0xFFFFFF) result = 0xFFFFFF;
            return result;
        }
        function resetBoundary(b,x,z){
            b.minX=(x<b.minX)?x:b.minX;
            b.minZ=(z<b.minZ)?z:b.minZ;
            b.maxX=(x>b.maxX)?x:b.maxX;
            b.maxZ=(z>b.maxZ)?z:b.maxZ;
        }
        return self;
    }
};


